import React, { Component } from 'react';
import './Input.css';
import logo from './logo.svg';


class Input extends Component {

	constructor (){
		super();
		this.handleChange = this.handleChange.bind( this );
		this.handleDrop = this.handleDrop.bind(this);
		this.supportFileApi = false;
		this.count = 0;
		this.state = {
			count: 0,
			valueInputFile: '',
			error: ''
		};
		if ( window.File && window.FileReader && window.FileList && window.Blob ) this.supportFileApi = true;
	}

	readFile (file){
		return new Promise((resolve,reject) => {
			let reader = new FileReader();

			reader.onload = () => resolve(reader.result); //      
			reader.onerror = () => reject('  ');

			if ( !file ){ //    File
				reject( ' ' );
			}else if ( file.type !== 'application/json' ){ //   
				reject( file.type? "  JSON type: " + file.type: "    " );
			}else if ( !file.size ){ //    
				reject( '  ' );
			}else {
				reader.readAsText( file ); //     
			}
		})
	}

	_amountObject (obj ){
		for ( let i in obj ){
			if ( typeof obj[i] === 'object' ){
				this.count++;
				if ( (this.count % 300) !== 1 ){  //  call stack  300  
					this._amountObject( obj[i] );
				}else {
					setTimeout ( () => this._amountObject( obj[i] ), 0);
				}
			}
		}
	}

	_setStateError(err){
		this.setState({
			error: err
		})
	}

	handleChange ( e ){ //   Change
		this.count = 0;  //     JSON file
		this._setStateError(''); //  
		this.setState({
			valueInputFile: ''
		});
		let file; //  File

		if ( e.dataTransfer ){ //     file
			file = e.dataTransfer.files[0]; // (  Drag'n drop )
		}else {
			file = e.target.files[0];   // (  input )

			this.setState({
				valueInputFile: e.target.value  //   file  input
			})
		}

		if ( this.supportFileApi ){ //    
			this.readFile( file )
				.then( text =>{
					let json = JSON.parse( text ); //   file JSON
					this._amountObject(json); //   Object  File JSON

					this.setState({
						count: this.count, //     
					});

				})
				.catch( err =>{
					this.setState({
						valueInputFile: '', //      File
					});

					if (typeof err === 'string'){ //    ,  Catch  Promise reject
						console.error(err);
						this._setStateError(err);
					}else {
						console.error('JSON invalid : ', err);
						this._setStateError('  JSON');
					}
				})
		}else {
			console.log('File API not supported');
			this._setStateError('    File API');
		}
	}

	handleDrop ( e ){
		e.target.style.backgroundColor = '#eee';
		this.handleChange( e );
		e.preventDefault(); //    
	}

	hangleDragOver ( e ){
		e.target.style.backgroundColor = '#ccc';
		e.preventDefault();  //    
	}

	handleDragLeave ( e ){
		e.target.style.backgroundColor = '#eee';
		e.preventDefault();  //    
	}


	render(){
		let name = 'Alexey';
		return (
			<div className="App">
				<header className="App-header">
					<img src={logo} className="App-logo" alt="logo" />
					<h1 className="App-title">Welcome to React, { name }</h1>
					<h1 className="App-title">Number of objects, { this.state.count }</h1>
				</header>
				<div
					className="drop"
					onDrop={ this.handleDrop }
					onDragOver={ this.hangleDragOver }
					onDragLeave={ this.handleDragLeave }
				>
					<input
						id="control"
						type="file"
						accept="application/json"
						value={ this.state.valueInputFile }
						onChange={ this.handleChange }
					/>
					<p>{ this.state.error ? this.state.error: '' }</p>
				</div>
			</div>
		);
	}
}
export default Input;


